import { Component, OnInit } from '@angular/core';

import * as Matter from 'matter-js';

@Component({
  selector: 'app-matter-game',
  templateUrl: './matter-game.component.html',
  styleUrls: ['./matter-game.component.css']
})
export class MatterGameComponent implements OnInit {


  constructor() { 
   
  }

  ngOnInit(): void {
    this.init()

  }


  init = ()=>{
    let engine = Matter.Engine.create()
    let render= Matter.Render.create({
      element: document.getElementById('matter'),
      engine:engine
    });    

    //set canvas width height
    render.canvas.setAttribute('width',"800")
    render.canvas.setAttribute('height',"600px")

    let ground = Matter.Bodies.rectangle(600,550,1200,60,{isStatic:true})
    let wallLeft = Matter.Bodies.rectangle(0,200,60,810,{isStatic:true})
    let wallRight = Matter.Bodies.rectangle(790,200,60,810,{isStatic:true})
    
    let boxA = Matter.Bodies.rectangle(400,200,80,80)
    let boxB = Matter.Bodies.rectangle(450,50,80,80)

    let mouse = Matter.Mouse.create(render.canvas)

    let mouseConstraint = Matter.MouseConstraint.create(engine, {
      mouse: mouse,

    });
    render.controller.mouse = mouse;

    let stack = Matter.Composites.stack(500,100,14,15,0,0,(x,y)=>{
      return Matter.Bodies.polygon(x,y,8,10)
    })
    let supp = Matter.Bodies.rectangle(600,400,300,20,{isStatic:true})
    let ball = Matter.Bodies.circle(150,450,30)
    let sling = Matter.Constraint.create({
      pointA:{x:150,y:400},
      bodyB:ball,
      stiffness: 0.0056
    })
    let firing = false;
    Matter.Events.on(mouseConstraint,'enddrag',function(e){
      if(e.body ===ball) firing = true;
    });
    Matter.Events.on(engine,'afterUpdate', function(){
      if(firing && Math.abs(ball.position.x-150)<20 && Math.abs(ball.position.y-400)<20){
        ball = Matter.Bodies.circle(150,450,20)
        Matter.World.add(engine.world,ball)
          sling.bodyB = ball;
          firing=false;
      } 
    })



    Matter.World.add(engine.world,[ground,supp,ball])
    Matter.World.add(engine.world,stack)
    Matter.World.add(engine.world,mouseConstraint)
    Matter.World.add(engine.world,sling)
    Matter.Engine.run(engine)
    Matter.Render.run(render)
    
  }
}
