import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-youtube-player',
  templateUrl: './youtube-player.component.html',
  styleUrls: ['./youtube-player.component.css']
})
export class YoutubePlayerComponent implements OnInit {

  url:string ;
  input: any;
  player: any;
  list:any;

  videoId: string;

  constructor() { 
    
  }

  ngOnInit(): void {
    this.input = document.querySelector('input#search')
    this.player = document.querySelector('div#player')
    this.list = document.querySelector('ul#videoIdList')
    this.init()
  }

  init(){
    console.log(this.player) 
    this.input.addEventListener('keyup',(event)=>{
      if(this.input.value!=null){
        this.url = this.input.value;
      }
      
      console.log(this.input.value)

    })

   
  }
  onYouTubeIframeAPIReady() {
    this.player = new YT.Player('player', {
      height: '360',
      width: '640',
      videoId: this.url,
    playerVars: { 'autoplay': 1, 'controls': 1 },
    events: {
      'onReady': this.onPlayerReady,
    }
    })
    ;
  }

  onPlayerReady(event) {
    event.target.playVideo();
  }

  addToList(){
    let pocket = document.createElement('li')
    pocket.textContent = this.url;
    console.log('bim')
    this.list.appendChild(pocket)
  }
  

  getThatVideo(){
    this.onYouTubeIframeAPIReady()
    
    console.log(this.player);
  }

  verif(): boolean{
    let got = false;
    let uRI = "https://www.youtube.com/watch?v="+this.url
   fetch(uRI, {
    method: 'GET',
    headers: {
      'Access-Control-Allow-Origin': '*',
    }}).then(resp => {
      console.log(resp)
      if(resp.status != 200){
        got = false;
      }
      else{
        got = true;
      }
      
    

    })
    return got;
  }


}
